a.navbar-item {
    color: #7a7a7a;
}

a.navbar-item:hover {
    color: #3c74db !important;
    border-radius: 5px;
}

.button.is-link:hover {
    color: white !important;
    background-color: #3c74db !important;
}

.button.is-link {
    color: #3c74db !important;
    border-color: #3c74db !important;
}

a.navbar-item.is-active {
    color: #3c74db !important;
    text-decoration: underline;
    text-underline-position: under;
    background: rgba(255, 255, 255, 0);
}
.header-image {
    background: -webkit-gradient(linear, right top, left top, from(rgba(240, 175, 35, 0)), to(#3c74dba1)),
        url(/static/media/header.83e7790f.jpg);
    background: linear-gradient(to left, rgba(240, 175, 35, 0), #3c74dba1),
        url(/static/media/header.83e7790f.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.button.is-white.is-outlined:hover {
    color: #3c74db !important;
}

li {
    width: 30%;
}

li.is-active span {
    color: #3c74db !important;
}

li span {
    color: white !important;
}

.tabs.is-boxed li a:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.drive {
    color: #3c74db !important;
}

.ship {
    color: #ffffff !important;
}

.screenshot {
    width: 250px;
    height: 444.5px;
}

.hit-title {
    width: 250px;
    word-break: normal;
}

.sec-level {
    padding-left: 90px;
    padding-right: 90px;
}

.top-title {
    color: #3c74db !important;
}

.sec-padding {
    padding-top: 80px;
    padding-bottom: 80px;
}

.footer {
    background: #3c74db !important;
    padding-top: 80px;
    padding-bottom: 80px;
}


ul#social li {
    display: inline;
}

ul#social li i {
    color: white;
    padding: 10px;
    font-size: 1.3em;
}

hr {
    opacity: 0.5 !important;
    height: 0.5px !important;
}

.text-wrap-benfits {
    width: 200px;
    word-break: normal;
}

.text-wrap-header {
